* {
    box-sizing: border-box; }
html {
    font-family: 'Poppins'; }
@media (prefers-reduced-motion: no-preference) {
    &:root {
        scroll-behavior: smooth; } }
body {
    line-height: 1.4; }

.kostyl {
    position: fixed; }
.container {
    position: relative;
    max-width: 1480px;
    margin: 0 auto; }
.back-button {
    background: transparent;
    padding: 0;
    height: 90px;
    width: 90px;
    border-radius: 100px;
    border: none;
    &:hover {
        fill: #7f7f7f; } }

button {
    border: none;
    padding: 0; }

.main-section {
    padding-top: 30px;
    &__back-button {
        position: absolute;
        left: 40px;
        top: 40px; }
    &__nav-container {
        background: white;
        display: flex;
        flex-direction: column;
        margin-bottom: 40px; }
    &__content-container {
        display: flex;
        flex-direction: column; }
    &__cards-box {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        gap: 20px;
        margin-bottom: 25px; }
    &__cards-item {}
    &__header {
        width: 100%;
        h1 {
            font-weight: 500;
            margin: 0;
            text-transform: uppercase;
            font-size: 60px;
            text-align: center; } } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 30px; }
    &__list-item {
        font-size: 18px;
        a {
            text-decoration: none;
            font-weight: normal;
            color: black;
            &:hover {
                padding-bottom: 2px;
                border-bottom: 2px solid;
                text-transform: capitalize; } } } }


.cards-item {
    position: relative;
    display: flex;
    align-items: flex-end;
    background: #e2e2e240;
    /* &__mask
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: 0
        background: rgba(0, 0, 0, .5)
        opacity: 0
        color: #fff
        font-size: 40px
        transition: opacity .4s linear
        &:hover
            opacity: .5 */
    a {
        display: block;
        position: relative;
        height: 800px; }
    &__img {
        object-fit: cover;
        object-position: center;
        width: 640px;
        height: 800px; } }

.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.8);
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    transition: all 0.8s ease 0s;
    &:target {
        opacity: 1;
        visibility: visible; }
    &__area {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0; }
    &__body {
        min-height: 100%;
        display: flex;
        align-items: center;
        justify-content: center; }
    &__content {
        position: absolute;
        max-width: 1480px; }
    &__close {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 20px;
        color: #000;
        text-decoration: none; } }


.slider {
    width: 700px;
    overflow: hidden; }
.slider-line {
    position: relative;
    left: 0;
    transition: all ease 1s;
    display: flex;
    width: 28000px;
    img {
        width: 700px;
        object-fit: contain; } }
.slider-next {
    background: 000;
    width: 60px;
    height: 60px;
    border-radius: 50px;
    background-image: url(../icons/back-icon.svg);
    transform: scaleX(-1);
    position: absolute;
    top: 515px;
    right: -80px; }
.slider-prev {
    background: 000;
    width: 60px;
    height: 60px;
    border-radius: 50px;
    background-image: url(../icons/back-icon.svg);
    position: absolute;
    top: 515px;
    left: -80px; }


